html, html *, html *::after, html *::before{
  box-sizing: border-box;
}

html{
  font-family: "Roboto", "Helvetica", "Arial", "sans-serif";
}

a{
  text-decoration: none;
}

body{
  margin: 0;
  padding: 0;
}

@media screen and (min-width: 576px){
  #root{
    width: 60%;
    margin: 0 auto;
    position: relative;
    box-shadow: 0px 0px 2px 2px #ccc;
  }
}





